body{
  background: #000022;
  background-image: url(/pomodoro-clock-react/static/media/night_sky.d7919084.jpg);
  background-repeat: no-repeat;
  background-size:auto;
}

.app-content {
  width: 600px;
  margin: auto;
  padding: 40px;
  align-content: center;
  text-align: center;
  display: flex;
}

.control-row {
  margin-bottom: 8px;
}

@media (max-width: 650px) {
  .app-content {
    margin: 20px 20px 0;
    width: auto;
  }
}

.row{
  align-self: center;
  padding-bottom: 4px;
}

#timeDisplay{
  background: #333;
  color: #999;
  margin: auto;
  width: 150px;
  border-radius: 25px;
  margin-bottom: 8px;
}

iframe,
#breakDisplay{
  margin: auto;
}
h2{
  margin: auto;
}

.btn{
  margin: auto;
}

label{
  color: #eee;
}

input[type=number]{
  background: #ccc;
  color: #222;
  border: #666;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

